export default function getHelperTextProps(fieldsWithError, field) {
  if (fieldsWithError.includes(fieldsWithError.find(el=>el.name===field))) {
    const field_with_error = fieldsWithError.find(el=>el.name===field);
    let error_text =  '' ;
    Object.keys(field_with_error.errors).forEach(function(error){ error_text+=field_with_error.errors[error] + ". " ;});
    return {"error":'error',  'helperText': `${error_text}`};
  }
}
